import { useState, useEffect } from "react";

import styles from './NewsFeed.module.css'

const NewsFeed = (numbers) => {
  const [items, setItems] = useState([]);
  const [isLoaded, setIsLoaded] = useState(false);
  const [error, setError] = useState(null);

  useEffect(() => {
    fetch("https://newsapi.org/v2/everything?q=cryptocurrency&apiKey=73e091e3927445679eaa1963e04a2d6e")
      .then((res) => res.json())
      .then(
        (result) => {
          setIsLoaded(true);
          setItems(result.articles);
          console.log(result)
        },
        // Note: it's important to handle errors here
        // instead of a catch() block so that we don't swallow
        // exceptions from actual bugs in components.
        (error) => {
          setIsLoaded(true);
          setError(error);
        }
      );

  }, []);

  if (error) {
    return (
      <div>
        <p>Error</p>
      </div>
    );
  } else if (!isLoaded) {
    return <div>Loading . . .</div>;
  } else {
    return (
        <section>
            {items.map((item, numbers) => (
            <a key={numbers} className={styles.articleLink} href={item.url} target="_blank">
                <div  className={styles.newsCont}>
                    <img className={styles.newsPic} src={item.urlToImage} alt="image"/>
                    <h3 className={styles.articleTitle}>{item.title}</h3>
                    <p className={styles.articleAuthor}>- {item.author}</p>
                </div>
            </a>
            ))}
        </section>
    );
  }
};

export default NewsFeed;
